body {
  font-family: "Kumbh Sans", sans-serif;
  font-size: 1.42rem;
  color: hsl(219deg, 9%, 45%);
  line-height: 2.5rem;
}
@media (min-width: 1200px) {
  body {
    font-size: 1.6rem;
  }
}

p {
  margin: 0;
  line-height: 2.5rem;
}
@media (min-width: 1200px) {
  p {
    line-height: 2.6rem;
  }
}

[class$=__contenedor] {
  padding: 0 2.3rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  [class$=__contenedor] {
    padding: 0 3.5rem;
  }
}
@media (min-width: 1200px) {
  [class$=__contenedor] {
    padding: 0;
    max-width: 111rem;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2 {
  font-weight: 700;
  margin: 0;
  font-family: "Kumbh Sans", sans-serif;
}

h1 {
  color: hsl(220deg, 13%, 13%);
  font-size: 2.8rem;
  line-height: 3.2rem;
  margin-bottom: 1.6rem;
}
@media (min-width: 1200px) {
  h1 {
    font-size: 4.4rem;
    line-height: 4.8rem;
    margin-bottom: 3.5rem;
  }
}

h2 {
  color: hsl(26deg, 100%, 55%);
  font-size: 1.2rem;
  line-height: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  margin-bottom: 1.4rem;
}
@media (min-width: 1200px) {
  h2 {
    font-size: 1.4rem;
    line-height: 1.7rem;
    margin-bottom: 1.9rem;
  }
}

button {
  background-color: transparent;
  border: none;
  padding: 0;
  margin: 0;
}
button:hover {
  cursor: pointer;
}

.header__barra {
  height: 6.8rem;
}
@media (min-width: 1200px) {
  .header__barra {
    height: 11.2rem;
    border-bottom: 0.2rem solid hsl(223deg, 64%, 98%);
  }
}
.header__barra, .header__lado-izquierdo, .header__lado-derecho {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__lado-izquierdo {
  justify-content: unset;
  gap: 1.6rem;
}
@media (min-width: 1200px) {
  .header__lado-izquierdo {
    gap: 5.8rem;
  }
}
.header__btn-burger {
  width: 1.6rem;
  height: 1.5rem;
  background-image: url(../img/icon-menu.svg);
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 1200px) {
  .header__btn-burger {
    display: none;
  }
}
.header__overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.75);
  transition-property: opacity;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  transform: translateX(-100%);
  transition-property: transform;
  transition-duration: 0.3s;
}
.header__overlay--activo {
  transform: translateX(0%);
}
@media (min-width: 1200px) {
  .header__overlay--activo {
    transform: translateX(-100%);
  }
}
.header__lado-derecho {
  justify-content: unset;
  gap: 2.2rem;
}
@media (min-width: 1200px) {
  .header__lado-derecho {
    gap: 4.5rem;
  }
}
@media (min-width: 768px) {
  .header__carrito {
    position: relative;
  }
}
.header__carrito-contenido {
  width: 35rem;
  position: absolute;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 1rem;
  top: 7.6rem;
  left: 50%;
  z-index: 3;
  transform: translateX(-50%);
  box-shadow: 0 5rem 5rem rgba(0, 0, 0, 0.1);
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s;
}
@media (min-width: 768px) {
  .header__carrito-contenido {
    transform: translateX(0%);
    left: unset;
    right: 0;
  }
}
@media (min-width: 1200px) {
  .header__carrito-contenido {
    left: -17.5rem;
    top: 5.6rem;
  }
}
.header__carrito-contenido--activo {
  opacity: 1;
  pointer-events: all;
}
.header__carrito-header {
  color: hsl(220deg, 13%, 13%);
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.4rem;
  text-align: left;
  padding: 2.7rem 2.4rem;
  border-bottom: 0.2rem solid hsl(223deg, 64%, 98%);
}
.header__carrito-body {
  min-height: 18.9rem;
  padding: 2.5rem 2.4rem;
}
.header__carrito-body--vacio {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header__carrito-vacio {
  font-weight: 700;
  font-size: 1.5rem;
  color: hsl(219deg, 9%, 45%);
}
.header__carrito-producto {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__carrito-producto:not(:last-of-type) {
  margin-bottom: 1.5rem;
}
.header__carrito-producto-img {
  width: 5rem;
  height: 5rem;
  border-radius: 0.4rem;
}
.header__carrito-producto-nombre, .header__carrito-producto-cantidadPrecio, .header__carrito-producto-precioTotal {
  color: hsl(220deg, 13%, 13%);
}
.header__carrito-producto-cantidadPrecio, .header__carrito-producto-precioTotal {
  display: inline-block;
}
.header__carrito-producto-precioTotal {
  font-weight: 700;
  margin-left: 0.8rem;
}
.header__carrito-producto-btns {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}
.header__carrito-producto-btnAumentar, .header__carrito-producto-btnEliminar, .header__carrito-producto-btnRestar {
  width: 2rem;
  height: 2rem;
  background-image: url(../img/icon-delete.svg);
  background-position: center;
  background-repeat: no-repeat;
}
.header__carrito-producto-btnAumentar, .header__carrito-producto-btnRestar {
  background-color: hsl(223deg, 64%, 98%);
  border-radius: 0.4rem;
}
.header__carrito-producto-btnAumentar {
  background-image: url(../img/icon-plus.svg);
}
.header__carrito-producto-btnRestar {
  background-image: url(../img/icon-minus.svg);
}
.header__carrito-checkoutBtn {
  height: 5.5rem;
  background-color: hsl(26deg, 100%, 55%);
  font-size: 1.6rem;
  font-weight: 700;
  text-align: center;
  color: hsl(0deg, 0%, 100%);
  text-decoration: none;
  transition: background-color 0.3s ease;
  border-radius: 1rem;
  box-shadow: 0px 3rem 1.8rem -1.5rem hsl(25deg, 100%, 94%);
  width: 100%;
  margin-top: 2.6rem;
}
.header__carrito-checkoutBtn:hover {
  cursor: pointer;
  background-color: #ffb780;
}
.header__usuario-img-c {
  width: 2.4rem;
  height: 2.4rem;
  border: 0.1rem solid transparent;
  transition: border-color 0.3s;
}
.header__usuario-img-c:hover {
  border-color: hsl(26deg, 100%, 55%);
}
@media (min-width: 1200px) {
  .header__usuario-img-c {
    border-radius: 50%;
    width: 5rem;
    height: 5rem;
  }
}
.header__usuario-img {
  width: 100%;
  height: 100%;
}

.nav {
  background-color: hsl(0deg, 0%, 100%);
  position: fixed;
  z-index: 12;
  top: 0;
  left: 0;
  width: 25rem;
  height: 100%;
  padding: 2.4rem 0 0 2.5rem;
  display: flex;
  flex-direction: column;
  gap: 1.7rem;
  transform: translateX(-100%);
  transition-property: transform;
  transition-duration: 0.3s;
}
.nav--mostrar {
  transform: translateX(0%);
}
@media (min-width: 1200px) {
  .nav {
    z-index: unset;
    position: unset;
    padding: 0;
    transform: translateX(0%);
    flex-direction: row;
    width: 100%;
    background-color: transparent;
  }
}
.nav__btn-cerrar {
  width: 1.6rem;
  height: 1.5rem;
  background-image: url(../img/icon-close.svg);
  background-repeat: no-repeat;
  background-size: contain;
  margin-bottom: 3.6rem;
}
@media (min-width: 1200px) {
  .nav__btn-cerrar {
    display: none;
  }
}
.nav__link {
  line-height: 2.1rem;
  font-weight: 700;
  color: hsl(220deg, 13%, 13%);
  position: relative;
  width: max-content;
  transition: color 0.3s;
}
@media (min-width: 1200px) {
  .nav__link {
    font-size: 1.5rem;
    line-height: 1.8rem;
    color: hsl(220deg, 14%, 75%);
    font-weight: 400;
  }
}
.nav__link::before {
  content: "";
  position: absolute;
  top: 0;
  left: -2.5rem;
  background-color: hsl(26deg, 100%, 55%);
  height: 100%;
  width: 0.4rem;
  opacity: 0;
  transition: opacity 0.3s;
}
@media (min-width: 1200px) {
  .nav__link::before {
    height: 0.4rem;
    width: 100%;
    top: unset;
    bottom: -4.6rem;
    left: 0;
  }
}
.nav__link:hover {
  color: hsl(220deg, 13%, 13%);
}
.nav__link:hover::before {
  opacity: 1;
}

.producto {
  max-width: 37.5rem;
  margin: 0 auto;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .producto {
    max-width: 55rem;
  }
}
@media (min-width: 1200px) {
  .producto {
    max-width: 101.8rem;
    margin-top: 9.1rem;
  }
}
.producto__flex {
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 1200px) {
  .producto__flex {
    flex-direction: row;
    justify-content: space-between;
  }
}
.producto__slider-c {
  width: 100%;
}
@media (min-width: 1200px) {
  .producto__slider-c {
    display: none;
  }
}
.producto__slider {
  width: 100%;
  height: 35rem;
  position: relative;
  overflow: hidden;
}
@media (min-width: 768px) {
  .producto__slider {
    height: 50rem;
  }
}
.producto__previo-btn, .producto__siguiente-btn {
  position: absolute;
  top: 50%;
  left: 1.5rem;
  transform: translateY(-50%);
  z-index: 2;
  width: 4rem;
  height: 4rem;
  background-color: hsl(0deg, 0%, 100%);
  background-image: url(../img/icon-previous.svg);
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
}
@media (min-width: 1200px) {
  .producto__previo-btn, .producto__siguiente-btn {
    display: none;
  }
}
.producto__siguiente-btn {
  left: unset;
  right: 1.5rem;
  background-image: url(../img/icon-next.svg);
}
.producto__img-c {
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition-property: all;
  transition-duration: 0.4s;
}
.producto__img-c--activo {
  opacity: 1;
}
.producto__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.producto__galeria {
  display: none;
}
@media (min-width: 1200px) {
  .producto__galeria {
    display: block;
    width: 44.5rem;
  }
}
.producto__galeria-c {
  width: 100%;
  height: 44.5rem;
  position: relative;
  overflow: hidden;
  border-radius: 1.5rem;
}
.producto__galeria-c:hover {
  cursor: pointer;
}
.producto__galeria-img-c {
  display: flex;
  align-items: center;
  border-radius: 1.5rem;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transform: translateY(100%);
  transition: transform 0.3s;
}
.producto__galeria-img-c--activo {
  transform: translateY(0%);
}
.producto__galeria-img {
  width: 100%;
  height: auto;
  border-radius: 1.5rem;
}
.producto__miniatura-c {
  margin-top: 3rem;
  align-items: center;
  justify-content: space-between;
  display: none;
}
@media (min-width: 1200px) {
  .producto__miniatura-c {
    display: flex;
  }
}
.producto__miniatura-img-c {
  border: 0.2rem solid transparent;
  transition: border 0.3s;
  border-radius: 1.2rem;
  overflow: hidden;
  position: relative;
  width: 9.2rem;
  height: 9.2rem;
}
.producto__miniatura-img-c::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.5);
  transform: translateY(100%);
  transition: transform 0.3s;
}
.producto__miniatura-img-c:hover::before {
  transform: translateY(0%);
}
.producto__miniatura-img-c--activo {
  border: 0.2rem solid hsl(26deg, 100%, 55%);
  transform: translateY(0%);
}
.producto__miniatura-img-c--activo::before {
  transform: translateY(0%);
}
.producto__miniatura-img {
  width: 100%;
  height: auto;
  object-fit: cover;
}
.producto__info-c {
  margin-top: 2.4rem;
  padding: 0 2.3rem;
}
@media (min-width: 1200px) {
  .producto__info-c {
    width: 44.5rem;
    margin: 0;
    padding: 0;
  }
}
.producto__precio-c {
  display: flex;
  align-items: center;
  margin-top: 3rem;
}
@media (min-width: 1200px) {
  .producto__precio-c {
    flex-wrap: wrap;
  }
}
.producto__precio {
  font-size: 3rem;
  line-height: 2.6rem;
  font-weight: 700;
  color: hsl(220deg, 13%, 13%);
}
.producto__descuento {
  width: 5.1rem;
  height: 2.7rem;
  background-color: hsl(25deg, 100%, 94%);
  color: hsl(26deg, 100%, 55%);
  margin-left: 1.6rem;
  font-size: 1.6rem;
  font-weight: 700;
  border-radius: 0.5rem;
  text-align: center;
}
.producto__precio-anterior {
  margin-left: auto;
  font-size: 1.6rem;
  font-weight: 700;
  color: hsl(220deg, 14%, 75%);
  text-decoration: line-through;
}
@media (min-width: 1200px) {
  .producto__precio-anterior {
    width: 100%;
    margin-top: 0.9rem;
  }
}
.producto__botonera {
  margin-top: 2.7rem;
  display: flex;
  flex-direction: column;
  gap: 1.7rem;
}
@media (min-width: 1200px) {
  .producto__botonera {
    gap: unset;
    flex-direction: row;
    flex-wrap: wrap;
  }
}
.producto__aumentar-decrementar-c {
  display: flex;
  align-items: center;
  height: 5.5rem;
  background-color: hsl(223deg, 64%, 98%);
  border-radius: 1rem;
  padding: 0 1rem;
}
@media (min-width: 1200px) {
  .producto__aumentar-decrementar-c {
    width: 15.7rem;
  }
}
.producto__decrementar-btn, .producto__aumentar-btn {
  height: 100%;
  width: 4rem;
  background-image: url(../img/icon-minus.svg);
  background-repeat: no-repeat;
  background-position: center;
}
.producto__cantidad {
  flex: 1;
  text-align: center;
  color: hsl(220deg, 13%, 13%);
  font-size: 1.6rem;
  font-weight: 700;
}
.producto__aumentar-btn {
  background-image: url(../img/icon-plus.svg);
}
.producto__agregar {
  height: 5.5rem;
  background-color: hsl(26deg, 100%, 55%);
  font-size: 1.6rem;
  font-weight: 700;
  text-align: center;
  color: hsl(0deg, 0%, 100%);
  text-decoration: none;
  transition: background-color 0.3s ease;
  border-radius: 1rem;
  box-shadow: 0px 3rem 1.8rem -1.5rem hsl(25deg, 100%, 94%);
  display: flex;
  align-items: center;
  gap: 1.6rem;
  justify-content: center;
}
.producto__agregar:hover {
  cursor: pointer;
  background-color: #ffb780;
}
@media (min-width: 1200px) {
  .producto__agregar {
    flex: 1;
    margin-left: 1.5rem;
  }
}
.producto__mensaje {
  font-style: italic;
  font-weight: 700;
}
.producto__mensaje--error {
  color: red;
}
.producto__mensaje--ok {
  color: green;
}
@media (min-width: 1200px) {
  .producto__mensaje {
    margin-top: 1.5rem;
  }
}
.producto__carrito-img path {
  fill: hsl(0deg, 0%, 100%);
}

.modal {
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(-100%);
  transition: all 0.3s;
}
@media (min-width: 1200px) {
  .modal--activo {
    transform: translateY(0%);
  }
}
.modal__overlay {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.75);
  width: 100%;
  height: 100%;
}
.modal__slider-btn-cerrar {
  position: absolute;
  top: 2rem;
  right: 2rem;
}
.modal__slider-btn-cerrar svg path {
  transition: all 0.3s;
}
.modal__slider-btn-cerrar:hover svg path {
  fill: hsl(26deg, 100%, 55%);
}
.modal__contenido {
  width: 44.5rem;
}
@media (min-width: 1400px) {
  .modal__contenido {
    width: 55rem;
  }
}
.modal__slider-c {
  position: relative;
  width: 100%;
  height: 44.5rem;
}
@media (min-width: 1400px) {
  .modal__slider-c {
    height: 55rem;
  }
}
.modal__slider-btn-previo, .modal__slider-btn-siguiente {
  position: absolute;
  top: 50%;
  left: -2.75rem;
  transform: translateY(-50%);
  width: 5.5rem;
  height: 5.5rem;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 50%;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal__slider-btn-previo svg path, .modal__slider-btn-siguiente svg path {
  transition: all 0.3s;
}
.modal__slider-btn-previo:hover svg path, .modal__slider-btn-siguiente:hover svg path {
  stroke: hsl(26deg, 100%, 55%);
}
.modal__slider-btn-siguiente {
  left: auto;
  right: -2.75rem;
}
.modal__slider-img-c {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 1.5rem;
  overflow: hidden;
  opacity: 0;
  transition: opacity 0.3s;
}
.modal__slider-img-c--activo {
  opacity: 1;
}
.modal__slider-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.modal__miniatura-c {
  margin-top: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2.9rem;
}
.modal__miniatura-img-c {
  border: 0.2rem solid transparent;
  width: 7.5rem;
  height: 7.5rem;
  border-radius: 1.2rem;
  overflow: hidden;
  position: relative;
  transition: border-color 0.3s;
}
.modal__miniatura-img-c::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.5);
  transform: translateY(100%);
  transition: transform 0.3s;
}
.modal__miniatura-img-c:hover::before {
  transform: translateY(0%);
}
.modal__miniatura-img-c--activo {
  border-color: hsl(26deg, 100%, 55%);
}
.modal__miniatura-img-c--activo::before {
  transform: translateY(0%);
}
@media (min-width: 1400px) {
  .modal__miniatura-img-c {
    width: 9.2rem;
    height: 9.2rem;
  }
}
.modal__miniatura-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}