@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {   

    &__barra {
        height: 6.8rem;

        @include m.desktop { 
            height: 11.2rem;
            border-bottom: .2rem solid v.$lightGrayishblue;
        }
    }

    &__barra,
    &__lado-izquierdo,
    &__lado-derecho {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__lado-izquierdo {
        justify-content: unset;
        gap: 1.6rem;

        @include m.desktop { 
            gap: 5.8rem;
        }
    }

    
    &__btn-burger {
        width: 1.6rem;
        height: 1.5rem;
        background-image: url(../img/icon-menu.svg);
        background-repeat: no-repeat;
        background-size: cover;

        @include m.desktop { 
            display: none;
        }
    }


    &__overlay {
        position: fixed;
        background-color: transparentize(v.$black, 0.25%);
        transition-property: opacity;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 10;
        transform: translateX(-100%);
        transition-property: transform;
        transition-duration: .3s;

        &--activo {
            transform: translateX(0%);

            @include m.desktop { 
                transform: translateX(-100%);
            }
        }
    }

    &__lado-derecho {
        justify-content: unset;
        gap: 2.2rem;

        @include m.desktop { 
            gap: 4.5rem;
        }
    }

    &__carrito {
        @include m.tablet { 
            position: relative;
        }
    }

    &__carrito-contenido {
        width: 35rem;
        position: absolute;
        background-color: v.$white;
        border-radius: 1rem;
        top: 7.6rem;
        left: 50%;
        z-index: 3;
        transform: translateX(-50%);
        box-shadow: 0 5rem 5rem rgba(0, 0, 0, 0.1);
        pointer-events: none;
        opacity: 0;
        transition: opacity .3s;

        @include m.tablet { 
            transform: translateX(0%);
            left: unset;
            right: 0;
        }

        @include m.desktop { 
            left: -17.5rem;
            top: 5.6rem;
        }

        &--activo {
            opacity: 1;
            pointer-events: all;
        }
    }

    &__carrito-header {
        color: v.$veryDarkBlue;
        font-size: 1.5rem;
        font-weight: 700;
        line-height: 1.4rem;
        text-align: left;
        padding: 2.7rem 2.4rem;
        border-bottom: .2rem solid v.$lightGrayishblue;
    }

    &__carrito-body {
        min-height: 18.9rem;
        padding: 2.5rem 2.4rem;

        &--vacio {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &__carrito-vacio {
        font-weight: 700;
        font-size: 1.5rem;
        color: v.$darkGrayishblue;
    }

    &__carrito-producto {
        display: flex;
        align-items: center;
        justify-content: space-between;

        &:not(:last-of-type) {
            margin-bottom: 1.5rem;
        }
    }

    &__carrito-producto-img {
        width: 5rem;
        height: 5rem;
        border-radius: .4rem;
    }

    &__carrito-producto-nombre,
    &__carrito-producto-cantidadPrecio,
    &__carrito-producto-precioTotal {
        color: v.$veryDarkBlue;
    }

    &__carrito-producto-cantidadPrecio,
    &__carrito-producto-precioTotal {
        display: inline-block;
    }

    &__carrito-producto-precioTotal {
        font-weight: 700;
        margin-left: .8rem;
    }

    &__carrito-producto-btns {
        display: flex;
        flex-direction: column;
        gap: .8rem;
    }

    &__carrito-producto-btnAumentar,
    &__carrito-producto-btnEliminar,
    &__carrito-producto-btnRestar {
        width: 2rem;
        height: 2rem;
        background-image: url(../img/icon-delete.svg);
        background-position: center;
        background-repeat: no-repeat;
    }

    &__carrito-producto-btnAumentar,
    &__carrito-producto-btnRestar {
        background-color: v.$lightGrayishblue;
        border-radius: .4rem;
    }

    &__carrito-producto-btnAumentar {
        background-image: url(../img/icon-plus.svg);
    }

    &__carrito-producto-btnRestar {
        background-image: url(../img/icon-minus.svg);
    }

    &__carrito-checkoutBtn {
        @include m.boton(v.$orange, v.$white);
        width: 100%;
        margin-top: 2.6rem;
    }



    &__usuario-img-c {
        width: 2.4rem;
        height: 2.4rem;
        border: .1rem solid transparent;
        transition: border-color .3s;

        &:hover {
            border-color: v.$orange;
        }

        @include m.desktop { 
            border-radius: 50%;
            width: 5rem;
            height: 5rem;
        }
    }

    &__usuario-img {
        width: 100%;
        height: 100%;
    }
}


.nav {
    background-color: v.$white;
    position: fixed;
    z-index: 12;
    top: 0;
    left: 0;
    // width: 66.6%;
    width: 25rem;
    height: 100%;
    padding: 2.4rem 0 0 2.5rem;
    display: flex;
    flex-direction: column;
    gap: 1.7rem;
    transform: translateX(-100%);
    transition-property: transform;
    transition-duration: .3s;

    &--mostrar {
        transform: translateX(0%);
    }

    @include m.desktop { 
        z-index: unset;
        position: unset;
        padding: 0;
        transform: translateX(0%);
        flex-direction: row;
        width: 100%;
        background-color: transparent;

    }

    &__btn-cerrar {
        width: 1.6rem;
        height: 1.5rem;
        background-image: url(../img/icon-close.svg);
        background-repeat: no-repeat;
        background-size: contain;
        margin-bottom: 3.6rem;

        @include m.desktop { 
            display: none;
        }
    }

    &__link {
        line-height: 2.1rem;
        font-weight: 700;
        color: v.$veryDarkBlue;
        position: relative;
        width: max-content;
        transition: color .3s;
    
        @include m.desktop { 
            font-size: 1.5rem;
            line-height: 1.8rem;
            color: v.$grayishBlue;
            font-weight: 400;
        }
    
        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: -2.5rem;
            background-color: v.$orange;
            height: 100%;
            width: .4rem;
            opacity: 0;
            transition: opacity .3s;
    
            @include m.desktop { 
                height: 0.4rem;
                width: 100%;
                top: unset;
                bottom: -4.6rem;
                left: 0;
            }
        }
    
        &:hover {
            color: v.$veryDarkBlue;
        }
    
        &:hover::before {
            opacity: 1;
        }
    }
    
}