@use 'variables' as v; 

/** Media Queries **/
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktop-xl {
    @media (min-width: v.$desktop-xl) {
        @content;
    }
}


@mixin boton($bg, $texto) {
    height: 5.5rem;
    background-color: $bg;
    font-size: 1.6rem;
    font-weight: 700;
    text-align: center;
    color: $texto;
    text-decoration: none;
    transition: background-color .3s ease;
    border-radius: 1rem;
    box-shadow: 0px 3rem 1.8rem -1.5rem v.$paleOrange;
    
    &:hover {
        cursor: pointer;
        background-color: lighten($bg, 20);
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}
