@use 'variables' as v; 
@use 'mixins' as m; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}

body {
    font-family: v.$font;
    font-size: 1.42rem;
    color: v.$darkGrayishblue;
    line-height: 2.5rem;

    @include m.desktop { 
        font-size: 1.6rem;
    }
}

p {
    margin: 0;
    line-height: 2.5rem;

    @include m.desktop { 
        line-height: 2.6rem;
    }
}

[class$="__contenedor"] {
    padding: 0 2.3rem;
    margin: 0 auto;

    @include m.tablet { 
        padding: 0 3.5rem;
    }

    @include m.desktop {
        padding: 0; 
        max-width: 111rem;
    }

}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    display: block;
}

h1, h2{
    font-weight: 700;
    margin: 0;
    font-family: v.$font;
}

h1 {
    color: v.$veryDarkBlue;
    font-size: 2.8rem;
    line-height: 3.2rem;
    margin-bottom: 1.6rem;

    @include m.desktop { 
        font-size: 4.4rem;
        line-height: 4.8rem;
        margin-bottom: 3.5rem;
    }
}

h2 {
    color: v.$orange;
    font-size: 1.2rem;
    line-height: 1.5rem;
    text-transform: uppercase;
    letter-spacing: 0.1rem;
    margin-bottom: 1.4rem;

    @include m.desktop { 
        font-size: 1.4rem;
        line-height: 1.7rem;
        margin-bottom: 1.9rem;
    }
}

button {
    background-color: transparent;
    border: none;
    padding: 0;
    margin: 0;

    &:hover {
        cursor: pointer;
    }
}