@use 'base/mixins' as m;
@use 'base/variables' as v;

.modal {
    position: fixed;
    // transform: translate(-50%, -50%);
    z-index: 20;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(-100%);
    transition: all .3s;

    @include m.desktop { 
        
        &--activo {
            transform: translateY(0%);
        }   
    }

    &__overlay {
        position: absolute;   
        background-color: transparentize(v.$black, 0.25%);
        width: 100%;
        height: 100%;
    }

    
    &__slider-btn-cerrar {
        position: absolute;
        top: 2rem;
        right: 2rem;
        
        svg path {
            transition: all .3s;
        }

        &:hover svg path {
            fill: v.$orange;
        }
    }



    &__contenido {
        width: 44.5rem;

        @include m.desktop-xl { 
            width: 55rem;
        }
    }

    &__slider-c {
        position: relative;
        width: 100%;
        height: 44.5rem;

        @include m.desktop-xl { 
            height: 55rem;
        }
    }

    &__slider-btn-previo,
    &__slider-btn-siguiente {
        position: absolute;
        top: 50%;
        left: -2.75rem;
        transform: translateY(-50%);
        width: 5.5rem;
        height: 5.5rem;
        background-color: v.$white;
        border-radius: 50%;
        z-index: 1;
        display: flex;
        align-items: center;
        justify-content: center;

        svg path {
            transition: all .3s;
        }

        &:hover svg path {
            stroke: v.$orange;
        }


    }

    &__slider-btn-siguiente {
        left: auto;
        right: -2.75rem;
    }

    &__slider-img-c {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 1.5rem;
        overflow: hidden;
        opacity: 0;
        transition: opacity .3s;

        &--activo {
            opacity: 1;
        }
    }

    &__slider-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }


    &__miniatura-c {
        margin-top: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 2.9rem;
    }

    &__miniatura-img-c {
        border: .2rem solid transparent;
        width: 7.5rem;
        height: 7.5rem;
        border-radius: 1.2rem;
        overflow: hidden;
        position: relative;
        transition: border-color .3s;

        &::before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background-color: rgba(255, 255, 255, 0.5);
            transform: translateY(100%);
            transition: transform .3s;
        }

        &:hover::before {
            transform: translateY(0%);
        }

        &--activo {
            border-color: v.$orange;

            &::before {
                transform: translateY(0%);
            }
        }

        @include m.desktop-xl { 
            width: 9.2rem;
            height: 9.2rem;
        }
    }

    &__miniatura-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

}