@use 'base/variables' as v;
@use 'base/mixins' as m;

.producto {
    max-width: 37.5rem;
    margin: 0 auto;
    margin-bottom: 5rem;

    @include m.tablet { 
        max-width: 55rem;
    }

    @include m.desktop { 
        max-width: 101.8rem;
        margin-top: 9.1rem;
    }

    &__flex {
        display: flex;
        align-items: center;
        flex-direction: column;

        @include m.desktop { 
            flex-direction: row;
            justify-content: space-between;
        }
    }

    // Slider

    &__slider-c {
        width: 100%;

        @include m.desktop { 
            display: none;
        }
    }

    &__slider {
        width: 100%;
        height: 35rem;
        position: relative;
        overflow: hidden;

        @include m.tablet { 
            height: 50rem;
        }
    }

    &__previo-btn,
    &__siguiente-btn {
        position: absolute;
        top: 50%;
        left: 1.5rem;
        transform: translateY(-50%);
        z-index: 2;
        width: 4rem;
        height: 4rem;
        background-color: v.$white;
        background-image: url(../img/icon-previous.svg);
        background-repeat: no-repeat;
        background-position: center;
        border-radius: 50%;

        @include m.desktop { 
            display: none;
        }
    }

    &__siguiente-btn {
        left: unset;
        right: 1.5rem;
        background-image: url(../img/icon-next.svg);
    }

    &__img-c {
        position: absolute;
        opacity: 0;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        transition-property: all;
        transition-duration: .4s;

        &--activo {
            opacity: 1;
        }
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    // Fin Slider


    // Galeria

    &__galeria {
        display: none;

        @include m.desktop { 
            display: block;
            width: 44.5rem;
        }
    }

    &__galeria-c {
        width: 100%;
        height: 44.5rem;
        position: relative;
        overflow: hidden;
        border-radius: 1.5rem;

        &:hover {
            cursor: pointer;
        }
    }

    &__galeria-img-c {
        display: flex;
        align-items: center;
        border-radius: 1.5rem;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        transform: translateY(100%);
        transition: transform .3s;

        &--activo {
            transform: translateY(0%);
        }
    }

    &__galeria-img {
        width: 100%;
        height: auto;
        border-radius: 1.5rem;
    }

    &__miniatura-c {
        margin-top: 3rem;
        align-items: center;
        justify-content: space-between;
        display: none;

        @include m.desktop { 
            display: flex;
        }
    }

    &__miniatura-img-c {
        border: .2rem solid transparent;
        transition: border .3s;
        border-radius: 1.2rem;
        overflow: hidden;
        position: relative;
        width: 9.2rem;
        height: 9.2rem;

        &::before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background-color: rgba(255, 255, 255, 0.5);
            transform: translateY(100%);
            transition: transform .3s;
        }

        &:hover::before {
            transform: translateY(0%);
        }

        &--activo {
            border: .2rem solid v.$orange;
            transform: translateY(0%);

            &::before {
                transform: translateY(0%);
            }
        }
    }

    &__miniatura-img {
        width: 100%;
        height: auto;
        object-fit: cover;

        
    }

    &__info-c {
        margin-top: 2.4rem;
        padding: 0 2.3rem;

        @include m.desktop { 
            width: 44.5rem;
            margin: 0;
            padding: 0;
        }
    }

    &__precio-c {
        display: flex;
        align-items: center;
        margin-top: 3rem;

        @include m.desktop { 
            flex-wrap: wrap;
        }
    }

    &__precio {
        font-size: 3rem;
        line-height: 2.6rem;
        font-weight: 700;
        color: v.$veryDarkBlue;
    }

    &__descuento {
        width: 5.1rem;
        height: 2.7rem;
        background-color: v.$paleOrange;
        color: v.$orange;
        margin-left: 1.6rem;
        font-size: 1.6rem;
        font-weight: 700;
        border-radius: .5rem;
        text-align: center;
    }

    &__precio-anterior {
        margin-left: auto;
        font-size: 1.6rem;
        font-weight: 700;
        color: v.$grayishBlue;
        text-decoration: line-through;

        @include m.desktop { 
            width: 100%;
            margin-top: 0.9rem;
        }
    }

    &__botonera {
        margin-top: 2.7rem;
        display: flex;
        flex-direction: column;
        gap: 1.7rem;

        @include m.desktop { 
            gap: unset;
            flex-direction: row;
            flex-wrap: wrap;
        }
    }

    &__aumentar-decrementar-c {
        display: flex;
        align-items: center;
        height: 5.5rem;
        background-color: v.$lightGrayishblue;
        border-radius: 1rem;
        padding: 0 1rem;

        @include m.desktop { 
            width: 15.7rem;
        }
    }

    &__decrementar-btn,
    &__aumentar-btn {
        height: 100%;
        width: 4rem;
        background-image: url(../img/icon-minus.svg);
        background-repeat: no-repeat;
        background-position: center;
    }

    &__cantidad {
        flex: 1;
        text-align: center;
        color: v.$veryDarkBlue;
        font-size: 1.6rem;
        font-weight: 700;
    }

    &__aumentar-btn {
        background-image: url(../img/icon-plus.svg);
    }

    &__agregar {
        @include m.boton(v.$orange, v.$white);
        display: flex;
        align-items: center;
        gap: 1.6rem;
        justify-content: center;

        @include m.desktop { 
            flex: 1;
            margin-left: 1.5rem;
        }
    }

    &__mensaje {
        font-style: italic;
        font-weight: 700;

        &--error {
            color: red;
        }

        &--ok {
            color: green;
        }

        @include m.desktop { 
            margin-top: 1.5rem;
        }
    }

    &__carrito-img {
        path {
            fill: v.$white;
        }
    }

    // Galeria de imagenes

}
